.hidden {
  display: none;
}
body {
  text-align: center;
  margin-top: 100px;
  text-shadow: 2px 2px #000000;
  color: rgb(145, 145, 145);
}
#login_form input {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-size: x-large;
}
#greeting {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-size: 50px;
  text-shadow: 1px 1px black;
  top: 15%;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}
#link {
  margin-top: 10px;
  margin-bottom: 10px;
  position: fixed;
  top: 10px;
  left: 10px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  color: rgb(145, 145, 145);
  font-size: large;
}
#coin {
  bottom: 10px;
  left: 10px;
  position: fixed;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  color: rgb(145, 145, 145);
  font-size: large;
}
#stock {
  bottom: 10px;
  right: 10px;
  position: fixed;
  margin-top: 10px;
  margin-bottom: 10px;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  color: rgb(145, 145, 145);
  font-size: large;
}
#stock_form {
  position: absolute;
  top: 33%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#stock_form input {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-size: x-large;
  width: 450px;
}
#stock_list {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 500px;
  max-height: 50px;
  margin-left: auto;
  margin-right: auto;
  font-size: xx-large;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  text-shadow: 1px 1px black;
  letter-spacing: 2px;
}
ul {
  list-style-type: square;
}
#quote {
  font-family: "Courier New", Courier, monospace;
  font-size: large;
  font-weight: 800;
  position: fixed;
  bottom: 7px;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: 100px;
  text-shadow: 1px 1px white;
}
#todo_form {
  position: fixed;
  top: 39%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#todo_form input {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-size: large;
}
#todo_list {
  position: fixed;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 500px;
  max-height: 50px;
  margin-left: auto;
  margin-right: auto;
  font-size: x-large;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  text-shadow: 1px 1px black;
}
#clock {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-size: 120px;
  position: absolute;
  top: 28%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-shadow: 3px 3px black;
  opacity: 80%;
}
#weather {
  font-family: "Courier New", Courier, monospace;
  position: absolute;
  top: 7px;
  right: 10px;
  text-shadow: 1px 1px white;
  font-weight: 800;
}
#q {
  font-size: x-large;
  font-weight: 800;
}
#coin_select {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-size: x-large;
  width: 500px;
  top: 15%;
  position: absolute;
  transform: translate(-50%, -50%);
}
#coin_ul {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-size: xx-large;
  top: 25%;
  left: 50%;
  position: fixed;
  transform: translate(-50%, -50%);
  max-width: 500px;
  max-height: 50px;
  color: rgb(145, 145, 145);
  text-shadow: 2px 2px black;
  letter-spacing: 2px;
}
button {
  left: 10%;
  align-items: center;
  appearance: none;
  background-color: #fff;
  border-radius: 24px;
  border-style: none;
  box-shadow: rgba(0, 0, 0, 0.2) 0 3px 5px -1px,
    rgba(0, 0, 0, 0.14) 0 6px 10px 0, rgba(0, 0, 0, 0.12) 0 1px 18px 0;
  box-sizing: border-box;
  color: #3c4043;
  cursor: pointer;
  display: inline-flex;
  fill: currentcolor;
  font-family: "Google Sans", Roboto, Arial, sans-serif;
  font-size: 14px;
  font-weight: 500;
  height: 20px;
  justify-content: center;
  letter-spacing: 0.25px;
  line-height: normal;
  max-width: 100%;
  overflow: visible;
  padding: 2px 2px;
  position: relative;
  text-align: center;
  text-transform: none;
  transition: box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1),
    opacity 15ms linear 30ms, transform 270ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: auto;
  will-change: transform, opacity;
  z-index: 0;
}

button:hover {
  background: #f6f9fe;
  color: #174ea6;
}

button:active {
  box-shadow: 0 4px 4px 0 rgb(60 64 67 / 30%),
    0 8px 12px 6px rgb(60 64 67 / 15%);
  outline: none;
}

button:focus {
  outline: none;
  border: 2px solid #4285f4;
}

button:not(:disabled) {
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 3px 0,
    rgba(60, 64, 67, 0.15) 0 4px 8px 3px;
}

button:not(:disabled):hover {
  box-shadow: rgba(60, 64, 67, 0.3) 0 2px 3px 0,
    rgba(60, 64, 67, 0.15) 0 6px 10px 4px;
}

button:not(:disabled):focus {
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 3px 0,
    rgba(60, 64, 67, 0.15) 0 4px 8px 3px;
}

button:not(:disabled):active {
  box-shadow: rgba(60, 64, 67, 0.3) 0 4px 4px 0,
    rgba(60, 64, 67, 0.15) 0 8px 12px 6px;
}

button:disabled {
  box-shadow: rgba(60, 64, 67, 0.3) 0 1px 3px 0,
    rgba(60, 64, 67, 0.15) 0 4px 8px 3px;
}
